<template>
  <Dropdown trigger="click"
            style="margin-left: 20px"
            @on-click="handle">
    <!-- <Icon type="md-settings"
          :size='20'
          color='#ccc' /> -->
    <slot name="dropBtn"></slot>
    <DropdownMenu slot="list">
      <DropdownItem v-for="(item,index) in data"
                    :key="index"
                    :name="index">
        <div style="font-size:20px">
          <Icon :type="item.icon"
                :size='25' />
          {{item.value}}
        </div>
      </DropdownItem>
    </DropdownMenu>
  </Dropdown>
</template>
<script>
export default {
  props: ['data'],
  methods: {
    handle (index) {
      console.log(index)
      this.$emit('selDropdownItem', index)
    }
  }
}
</script>