<template>
  <div class="edit-box">
    <!-- 返回首页 对话框 -->
    <Modal v-model="config.backHome"
           title="返回首页？"
           @on-ok="ok"
           @on-cancel="cancel"
           style="z-index:2000">
      <p>是否保存所有更改</p>
    </Modal>
    <!-- 删除文本集 -->
    <Modal v-model="config.deleteArtGrop"
           title="删除文本集"
           @on-ok="deleteArtGropFn"
           @on-cancel="cancel">
      <!-- 这里做一下记录 找了一天的错误；这里之所以要加上gropList.length=0的情况是因为当删除所有文本集的时候
           只写gropList[config.gropListActiveIndex].value这里会报错 -->
      <p>删除文本集将删除该文本集下所有文章，确定删除{{gropList.length>0?gropList[config.gropListActiveIndex].value:''}}文本集？</p>
    </Modal>

    <div class="grops">
      <div class="backHome"
           @click="goHome">返回首页</div>
      <div class="newGrop">
        <span class="newGropIcon"></span>
        <div @click="config.newGropInput = !config.newGropInput"
             class="newGropBtn">新建文集</div>
        <div class="hidden-box"
             :class="{'showBox':config.newGropInput}">
          <input type="text"
                 placeholder="请输入文本集名称…"
                 v-model="config.newGropValue">
          <div class="btn-box">
            <button class="submit"
                    @click="addGrop">提交</button>
            <button @click="config.newGropInput = false">取消</button>
          </div>
        </div>
      </div>
      <ul class="gropList">
        <li v-for="(item,index) in gropList"
            :key="index"
            :class="{activeLi:index===config.gropListActiveIndex}"
            @click="config.gropListActiveIndex = index">{{item.value}}
          <my-dropdown :data="config.artGropDropdown"
                       @selDropdownItem="dropdownItemClick($event)">
            <Icon type="md-settings"
                  :size='20'
                  color='#ccc'
                  slot="dropBtn"
                  class="settingIcon"
                  v-show="index === config.gropListActiveIndex" />
          </my-dropdown>
        </li>
      </ul>
    </div>
    <div class="arts">
      <div class="newArtBtn"
           @click="addNewArt"><span class="addIcon"></span>新建文章</div>
      <arts-list>
        <template v-slot:artList>
          <art-item v-for="(item,index) in artsList"
                    :key="index"
                    :data='item'
                    :show='selID === item.id'
                    @changeSelID="selID = $event"></art-item>
        </template>
      </arts-list>
    </div>
    <div class="edit-area">
      <md-edit v-if="config.edit" />
      <span v-if="!config.edit"
            class="bgFont">温州商学院</span>
    </div>
  </div>
</template>
<script>
import mdEdit from './MarkDownEdit.vue'
import ArtsList from '@/components/common/articleList/ArtList.vue'
import ArtItem from '@/components/editArtItem/EditArt.vue'
import MyDropdown from '@/components/common/dropdown/Dropdown.vue'
export default {
  components: {
    mdEdit,
    ArtsList,
    ArtItem,
    MyDropdown
  },
  data () {
    return {
      config: {
        backHome: false, // 返回首页弹窗控制
        newGropInput: false, // 新建文本集显示控制
        deleteArtGrop: false, // 删除文本集对话框控制
        updateArtGrop: false, // 修改文本集对话框控制
        deleteArtGropIndex: 0, // 要删除的文本集的index
        newGropValue: '', // 新建的文本集的值
        edit: false, // 编辑区显示控制
        gropListActiveIndex: 0, // 当前选中的文本集的index
        artGropDropdown: [  // 
          { name: 'updateArtGrop', icon: 'ios-create-outline', value: '修改文集' },
          { name: 'deleteArtGrop', icon: 'ios-trash-outline', value: '删除文集' }
        ]
      },
      gropList: [
        { id: '10001', value: '心情' },
        { id: '10002', value: '日记本' },
        { id: '10003', value: '笔记' }
      ],
      artsList: [
        { id: '1012', title: '标题五', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1004', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1005', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1006', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1007', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1008', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1009', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1010', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1011', title: '标题四', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1003', title: '标题三', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1002', title: '标题二', content: '测试文本测试文本测试文本测试文本测试文本' },
        { id: '1001', title: '标题一', content: '测试文本测试文本测试文本测试文本测试文本' }
      ],
      selID: '1001'
    }
  },
  methods: {
    goHome () {
      this.config.backHome = true
      // this.$router.goback()
    },
    ok () {
      // console.log('确定')
    },
    cancel () {
      // console.log('取消')
    },
    addGrop () {
      if (!(this.config.newGropValue)) {
        return null
      }
      this.config.newGropInput = false
      let newGrop = {}
      newGrop.id = (this.gropList[this.gropList.length - 1].id * 1 + 1) + ''
      newGrop.value = this.config.newGropValue
      this.gropList.push(newGrop)
      this.config.newGropValue = ''
    },
    addNewArt () {
      let newArt = {
        title: this.getDate(),
        id: (this.artsList[0].id * 1 + 1) + '',
        content: ''
      }
      this.artsList.unshift(newArt)
      this.selID = newArt.id
    },
    // 获取当前日期 
    getDate () {
      var date = new Date()
      return `${date.getFullYear()}-${date.getMonth()}-${date.getDate()}`
    },
    // 删除文集
    deleteArtGropFn () {
      this.gropList.splice(this.config.gropListActiveIndex, 1)
      this.config.gropListActiveIndex = 0// 删除文集时
    },
    dropdownItemClick (index) {
      if (index === 0) {
        this.updateArtGrop = true
      } else {
        this.config.deleteArtGrop = true
      }
    }
  }
}
</script>

<style lang="less" scope>
.edit-box {
  width: 100vw;
  height: 100vh;
  display: flex;
  font-size: 20px;
  .grops {
    background-color: #404040;
    width: 15%;
    display: flex;
    flex-direction: column;
    .backHome {
      margin: 30px 25px;
      border-radius: 30px;
      color: rgb(245, 119, 73);
      font-size: 22px;
      line-height: 50px;
      border: 1px solid rgb(245, 119, 73);
    }
    .backHome:hover,
    .newGrop:hover {
      cursor: pointer;
    }
    .newGrop {
      display: flex;
      justify-content: flex-start;
      padding: 0 10px;
      align-items: center;
      flex-wrap: wrap;
      .newGropIcon {
        position: relative;
        width: 15px;
        height: 15px;
        top: -2px;
      }
      .newGropIcon::before,
      .newGropIcon::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 15px;
        height: 4px;
        background-color: white;
      }
      .newGropIcon::after {
        transform: rotate(90deg);
      }
      .newGropIcon::before {
        transform: rotate(0deg);
      }
      .newGropBtn {
        color: white;
        font-size: 20px;
        margin-left: 15px;
      }
      .hidden-box.showBox {
        max-height: 200px;
      }
      .hidden-box {
        width: 100%;
        font-size: 18px;
        transition: all 0.2s ease-in-out;
        max-height: 0px;
        > input {
          width: 100%;
          background-color: #595959;
          border: none;
          height: 35px;
          line-height: 35px;
          color: #ccc;
          margin-top: 15px;
          padding: 0 15px;
        }
        .btn-box {
          padding: 15px;
          button {
            font-size: 20px;
            background-color: rgba(223, 220, 220, 0);
            border: none;
            color: rgb(143, 142, 142);
            height: 40px;
            width: 80px;
          }
          button:hover {
            cursor: pointer;
          }
          .submit {
            border: 1px solid #42c02e;
            color: #42c02e;
            border-radius: 20px;
          }
        }
      }
    }
    .gropList {
      width: 100%;
      flex-grow: 1;
      background-color: #404040;
      padding: 20px 0;
      list-style: none;
      > li {
        height: 60px;
        line-height: 60px;
        color: white;
        font-size: 18px;
        text-align: start;
        padding: 0 20px;
        transition: all 0.3s;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      > li:hover {
        background-color: rgba(223, 220, 220, 0.2);
        cursor: pointer;
      }
      .activeLi {
        border-left: 4px solid rgb(247, 136, 95);
        background-color: rgba(223, 220, 220, 0.2);
      }
    }
  }
  .newArtBtn:hover {
    cursor: pointer;
    color: #404040;
    span.addIcon {
      background-color: #404040;
    }
  }
  .arts {
    color: #888;
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    overflow-y: scroll;
    .newArtBtn {
      padding: 25px 30px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #ccc;
      .addIcon {
        display: block;
        width: 20px;
        height: 20px;
        position: relative;
        border-radius: 50%;
        background-color: #888;
        margin-right: 10px;
      }
      .addIcon::after,
      .addIcon::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 12px;
        height: 3px;
        background-color: white;
      }
      .addIcon::before {
        transform: rotate(0deg);
      }
      .addIcon::after {
        transform: rotate(90deg);
      }
    }
  }
  .edit-area {
    background-color: #f2f2f2;
    width: 65%;
    display: flex;
    justify-content: center;
    align-items: center;
    .v-note-wrapper {
      z-index: 0;
    }
    .bgFont {
      color: #e6e6e6;
      font-size: 60px;
    }
  }
}
</style>