<style lang="less" scoped>
.article-box:hover {
  cursor: pointer;
  color: #404040;
  background-color: #ddd;
}
.article-box.active {
  border-left: 6px solid #ec7259;
  background-color: #ddd;
  color: #404040;
}
.article-box {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #ccc;
  padding: 10px 15px;
  height: 120px;
  transition: all 0.2s;
  .main {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    flex-grow: 1;
    .icon {
      flex-grow: 1;
    }
    .describe {
      flex-grow: 5;
      display: flex;
      flex-direction: column;
      width: 70%;
      align-items: flex-start;
      justify-content: space-between;
      height: 70px;
      .title {
        font-size: 22px;
        padding: 0 10px;
      }
      .content {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 16px;
        width: 100%;
      }
    }
    .config {
      flex-grow: 1;
    }
  }
  .count {
    font-size: 12px;
    align-self: flex-start;
  }
}
</style>
<template>
  <div class="article-box"
       :class="{active:show}"
       @click="changeSel(data.id)">
    <div class="main">
      <div class="icon">
        <Icon type="ios-document"
              :size='35'
              :color='"#888"' />
      </div>
      <div class="describe">
        <div class="title">{{data.title}}</div>
        <div class="content"
             v-show="show">{{data.content}}</div>
      </div>
      <div class="config"
           v-show="show">
        <Dropdown trigger="click"
                  style="margin-left: 20px">
          <Icon type="md-settings"
                :size='25'
                color='#888' />
          <DropdownMenu slot="list">
            <DropdownItem v-for="(item,index) in dropList"
                          :key="index"
                          :name="index">
              <div style="font-size:20px">
                <Icon :type="item.icon"
                      :size='25' />
                {{item.value}}
              </div>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>
      </div>
    </div>
    <div class="count"
         v-show="show">字数:{{data.content.length}}</div>
  </div>
</template>
<script>
// import MyDropdown from '@/components/common/dropdown/Dropdown.vue'
export default {
  props: ['data', 'show'],
  methods: {
    changeSel (id) {
      this.$emit("changeSelID", id)
    }
  },
  data () {
    return {
      dropList: [
        { icon: '', value: '删除文章' },
        { icon: '', value: '移动文章' },
        { icon: '', value: '直接发布' },
        { icon: '', value: '禁止转载' },
      ]
    }
  },
  components: {
    // MyDropdown
  }
}
</script>
